<template>
  <div class="shopbag">
    <div class="shopbag-dh">
      <van-nav-bar
        title="购物袋"
        left-text="返回"
        left-arrow
        @click-left="onClickLeft"
        @click-right="onIsedit"
      >
        <template #right v-if="ShopbagData.length>0">
          <div>{{isedit ?'完成':'编辑'}}</div>
        </template>
      </van-nav-bar>
    </div>
    <div class="shopbag-top"></div>
    <div v-if="ShopbagData.length > 0">
      <div class="shopbag-body">
        <div class="shopbagitembox">
          <div class="shopbagitem" v-for="(item,index) in ShopbagData" :key="index">
            <van-swipe-cell>
              <van-cell :border="false" value="内容">
                <div class="cellitem">
                  <div class="checkbox">
                    <van-checkbox
                      v-model="item.isChick"
                      icon-size="24px"
                      name="bbb"
                      checked-color="#0C34BA"
                      @click="selectitem"
                    ></van-checkbox>
                  </div>
                  <div class="imgbox">
                    <img :src="item.small_img" alt />
                  </div>
                  <div class="smbox">
                    <div class="textbox">
                      <div class="namebox">
                        <p>
                          <strong>{{item.name}}</strong>
                          <span>{{item.rule}}</span>
                        </p>
                      </div>
                      <div class="pricebox">
                        <span>￥{{item.price}}</span>
                        <van-stepper
                          theme="round"
                          button-size="24"
                          checked-color="#0C34BA"
                          disable-input
                          v-model="item.count"
                          @change="changeCount(item)"
                        />
                      </div>
                    </div>
                  </div>
                </div>
              </van-cell>
              <template #right>
                <van-button @click="delitem(item.sid,index)" square type="danger" text="删除" />
              </template>
            </van-swipe-cell>
          </div>
        </div>
      </div>
      <div class="shopbag-foot">
        <van-submit-bar v-show="!isedit" :price="maxNum" @submit="goOrder" button-text="结算">
          <van-checkbox
            icon-size="24px"
            checked-color="#0C34BA"
            @click="selectAll"
            v-model="checkedAll"
          >全选</van-checkbox>
        </van-submit-bar>
        <van-submit-bar v-show="isedit" :price="maxNum" @submit="delanyItem" button-text="删除选中">
          <van-checkbox
            icon-size="24px"
            checked-color="#0C34BA"
            @click="selectAll"
            v-model="checkedAll"
          >全选</van-checkbox>
          <van-checkbox
            icon-size="24px"
            checked-color="#0C34BA"
            @click="ReverseAll"
            v-model="ReverseAllnum"
          >反选</van-checkbox>
        </van-submit-bar>
      </div>
    </div>
    <!-- 空显示 -->
    <div v-else class="konbox">
      <van-empty description="空空如也">
        <van-button round type="danger" class="bottom-button" color="#3a63f1" @click="gohome">去加购吧!</van-button>
      </van-empty>
    </div>

    <div class="bg" v-if="ShopbagData.length > 0"></div>
  </div>
</template>

<script>
import "../../assets/less/shopbag.less";
import { Toast } from "vant";
import { utils } from "../../assets/js/utils";
export default {
  name: "Shopbag",
  created() {
    this.findShopbagData();
  },
  data() {
    return {
      isedit: false,
      itemchecked: false,
      checkedAll: false,
      ReverseAllnum: false,
      // 购物车数据
      ShopbagData: [],
      //总价格
      maxNum: 2020,
    };
  },
  methods: {
    // 返回上一级
    onClickLeft() {
      // this.$router.push("/home");
      this.$router.go(-1);
    },
    //点击编辑
    onIsedit() {
      this.isedit = !this.isedit;
    },
    // 选择全部
    selectAll() {
      this.ShopbagData.map((v) => {
        v.isChick = this.checkedAll;
      });
      //计算价格
      this.addNum();
    },
    // 反选全部
    ReverseAll() {
      this.ShopbagData.map((v) => {
        v.isChick = !v.isChick;
      });
      //计算价格
      this.addNum();
    },
    // 查询购物袋数据
    findShopbagData() {
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return;
      }
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url: "/findAllShopcart",
        params: {
          appkey: this.appkey,
          tokenString,
        },
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data.code == 5000) {
            result.data.result.map((v) => {
              v.isChick = false;
            });
          } else {
            this.$toast(result.data.msg);
          }

          this.ShopbagData = result.data.result;
          //计算价格
          this.addNum();
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    // 判断是否全选
    selectitem() {
      //计算价格
      this.addNum();
      for (let i = 0; i < this.ShopbagData.length; i++) {
        if (!this.ShopbagData[i].isChick) {
          this.checkedAll = false;
          return;
        }
      }
      this.checkedAll = true;
    },
    // 封装删除购物商品方法
    delCommodity(sids) {
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return;
      }
      // 参数序列化
      let data = utils.queryString({
        appkey: this.appkey,
        tokenString,
        sids: JSON.stringify(sids),
      });

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      return this.axios({
        method: "post",
        url: "/deleteShopcart",
        data,
      });
    },
    //删除单个购物商品
    delitem(sid, index) {
      this.delCommodity([sid])
        .then((result) => {
          this.$toast.clear();
          if (result.data.code == 7000) {
            this.ShopbagData.splice(index, 1);
          }
          this.$toast(result.data.msg);
          //计算价格
          this.addNum();
        })
        .catch((err) => {});
    },
    //删除选中个商品
    delanyItem() {
      let sids = [];
      this.ShopbagData.map((v) => {
        if (v.isChick == true) {
          sids.push(v.sid);
        }
      });
      this.delCommodity(sids)
        .then((result) => {
          this.$toast.clear();
          if (result.data.code == 7000) {
            for (let i = this.ShopbagData.length - 1; i >= 0; i--) {
              if (this.ShopbagData[i].isChick) {
                this.ShopbagData.splice(i, 1);
              }
            }
          }
          this.$toast(result.data.msg);
          //计算价格
          this.addNum();
        })
        .catch((err) => {});
    },
    //修改商品数量
    changeCount(item) {
      //计算价格
      this.addNum();

      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return;
      }
      let data = utils.queryString({
        appkey: this.appkey,
        tokenString,
        sid: item.sid,
        count: item.count,
      });
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "post",
        url: "/modifyShopcartCount",
        data,
      })
        .then((result) => {
          this.$toast.clear();

          if (result.data.code != 6000) {
            this.$toast(result.data.msg);
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    //计算价格
    addNum() {
      let addnum = 0;
      this.ShopbagData.map((v) => {
        if (v.isChick) {
          addnum += v.price * v.count;
        }
      });

      this.maxNum = addnum * 100;
    },
    //跳转结算界面,并传值
    goOrder() {
      let bool = this.ShopbagData.every((v) => {
        return v.isChick == false;
      });
      if (bool) {
        this.$toast("请至少加购一件商品");
        return;
      }

      //查找需要购买的商品, sid
      let sids = [];
      this.ShopbagData.map((v) => {
        if (v.isChick) {
          sids.push(v.sid);
        }
      });

      sids = sids.join("-");
      //
      this.$router.push({
        name: "Pay",
        query: { sids },
      });
    },
    //跳转home界面
    gohome() {
      this.$router.push("/main");
    },
  },
};
</script>
